@import url(https://fonts.googleapis.com/css?family=Staatliches);
@import url(https://fonts.googleapis.com/css?family=ABeeZee);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'ABeeZee', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #6D8EA0;
}

.App {
  text-align: center;
}

.App-title {
  font-family: 'Staatliches', cursive;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #182825;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App-footer {
  background-color: #182825;
  padding: 20px 0;
}

.App-created {
  margin: 0;
  color: #fff;
}


.App-link {
    color: #22AED1;
}
.MoviesList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0;
}

.MoviesList-item {
    list-style: none;
    width: 300px;
    margin: 20px;
    color: #fff;
}

.MoviesList-item:hover {
    color: #06D6A0;
}

.MoviesList-img {
    width: 100%;
    height: 440px;
    background-size: cover;
    display: block;
    background-position: center;
}

.MoviesList-img, .MoviesList-tiltle, .MoviesList-year {
    transition: all 500ms ease;
}

.MoviesList-item:hover .MoviesList-img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0 17px 65px 0 rgba(5,24,51,.35);
}

.MoviesList-error {
    margin-bottom: 50px;
}
.Search {
    position: relative;
    width: 700px;
    margin: 30px auto;
    max-width: 100%;
    padding: 0 8px;
}

.Search-input {
    width: 100%;
    height: 50px;
    border: 0;
    color: #6D8EA0;
    font-size: 16px;
    padding: 5px 10px;
    border-radius: 35px;
    outline: none;
    box-shadow: 0 17px 65px 0 rgba(5,24,51,.35);
}

.Search-buttton {
    width: 43px;
    height: 42px;
    border: 0;
    text-transform: uppercase;
    font-size: 14px;
    transition: all 500ms ease;
    cursor: pointer;
    position: absolute;
    top: 4px;
    right: 12px;
    border-radius: 40px;
    outline: none;
    background: #6D8EA0;
}

.Search-buttton:hover {
    background: #22AED1;
}

.Search-message {
    margin: 10px 0;
    color: #fff;
}
